import './style.css'
import * as THREE from 'three'

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { Loader, RedFormat } from 'three'
import gsap from 'gsap'

var parent = document.querySelector(".modal-parent"),
    parent2 = document.querySelector(".modal-parent2"),
    parent3 = document.querySelector(".modal-parent3"),
    X = document.querySelector(".X"),
    X2 = document.querySelector(".X2"),
    X3 = document.querySelector(".X3");


X.addEventListener("click", disappearX);
function disappearX() {
  parent.style.display = "none";
}

X2.addEventListener("click", disappearX2);
function disappearX2() {
  parent2.style.display = "none";
}
X3.addEventListener("click", disappearX3);
function disappearX3() {
  parent3.style.display = "none";
}


// load
const loader = new THREE.TextureLoader()

const EarthTexture = loader.load('./assets/8k_earth_daymap.jpg')
const star = loader.load('./assets/star.png')
const pointert = loader.load('./assets/pointer.png')
const WVinf = loader.load('./assets/WorldView.png')
const Falcon = loader.load('./assets/Falcon.png')
const Mars = loader.load('./assets/Mars.png')

// Debug

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

// Objects
const geometry = new THREE.SphereBufferGeometry(.7, 32, 32);
const particlesG = new THREE.BufferGeometry
const particlesc = 2000
const wvg = new THREE.PlaneGeometry( .06, .06 )
const wvgt = new THREE.PlaneGeometry( .00000640, .00000210 )
const flg = new THREE.PlaneGeometry( .06, .06 )
const flgt = new THREE.PlaneGeometry( .00000416, .00000210 )
const mrg = new THREE.PlaneGeometry( .06, .06 )
const mrgt = new THREE.PlaneGeometry( .00000385, .00000220 )


const wvpivot = new THREE.Mesh()


const posArray =  new Float32Array(particlesc * 3)

for(let i = 0; i < particlesc * 3; i++){
    posArray[i] = (Math.random() - 0.5) * (Math.random() * 9)
}

particlesG.setAttribute('position', new THREE.BufferAttribute(posArray, 3))

// Materials
const pt = new THREE.MeshBasicMaterial({
    map: pointert,
    side: THREE.DoubleSide,
    transparent: true
})

const wvmt = new THREE.MeshBasicMaterial({
    map: WVinf,
    side: THREE.DoubleSide,
    transparent: true
})

const mrmt = new THREE.MeshBasicMaterial({
    map: Mars,
    side: THREE.DoubleSide,
    transparent: true
})

const falconmt = new THREE.MeshBasicMaterial({
    map: Falcon,
    side: THREE.DoubleSide,
    transparent: true
})

const Earthmat = new THREE.MeshStandardMaterial({
    map: EarthTexture
})

const stars = new THREE.PointsMaterial({
    size: 0.005,
    map: star,
    transparent: true,
    opacity: 0.8
})

// Mesh
function coordinate(p) {
    let lat = (p.lat) * (Math.PI/180)
    let lng = (p.lng+90) * (Math.PI/180)

    let x = (Math.sin(lng)*Math.cos(lat)) * 0.75
    let z = (Math.cos(lng)*Math.cos(lat)) * 0.75
    let y = -(Math.sin(lat)) * 0.75

    return {
        x,y,z
    }
}

let flcm = {
    lat: -8.71666,
    lng: 167.733333
}

let mrm = {
    lat: -45.6167,
    lng: 63.3167
}

let flc = coordinate(flcm)


let mr = coordinate(mrm)


const sphere = new THREE.Mesh(geometry,Earthmat)
const particlesM = new THREE.Points(particlesG, stars)
const WVr = new THREE.Mesh(wvg,pt)
const WVrt = new THREE.Mesh(wvgt,wvmt)
const FLr = new THREE.Mesh(flg,pt)
const FLrt = new THREE.Mesh(flgt,falconmt)
const MRr = new THREE.Mesh(mrg,pt)
const MRrt = new THREE.Mesh(mrgt,mrmt)
//sphere.position.z = 1
WVr.position.y = .75
FLr.position.set(flc.x, flc.y, flc.z)
MRr.position.set(mr.x, mr.y, mr.z)
scene.add(sphere, particlesM, WVr, wvpivot)
WVr.userData.clicable = true
FLr.userData.clicable = true
MRr.userData.clicable = true
WVr.userData.wvl = true
FLr.userData.fll = true
MRr.userData.mrl = true
wvpivot.add(WVr)
WVr.add(WVrt)
sphere.add(FLr)
FLr.add(FLrt)
sphere.add(MRr)
MRr.add(MRrt)



let wv1 = []
let fl1 = []
let mr1 = []
let clicable = []
scene.traverse((object) => {
    if (object.userData.clicable)
        clicable.push(object)
})
scene.traverse((object) => {
    if (object.userData.wvl)
        wv1.push(object)
})
scene.traverse((object) => {
    if (object.userData.fll)
        fl1.push(object)
})
scene.traverse((object) => {
    if (object.userData.mrl)
        mr1.push(object)
})


// Lights

const pointLightMain = new THREE.PointLight(0xffffff, 1)
pointLightMain.position.x = 2
pointLightMain.position.y = 3
pointLightMain.position.z = 4
scene.add(pointLightMain)

const pointLightBack = new THREE.PointLight(0xffffff)
pointLightBack.intensity = 0.6
pointLightBack.position.x = -2
pointLightBack.position.y = -3
pointLightBack.position.z = -1.2
scene.add(pointLightBack)




/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 2
scene.add(camera)

// Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true
/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor(new THREE.Color('#010005'), 1)

const raycaster = new THREE.Raycaster();
const pointer = new THREE.Vector2();
const click = new THREE.Vector2();

window.addEventListener('mousemove', (event) => {
    pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
})

window.addEventListener('click', (event) => {
    click.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	click.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    raycaster.setFromCamera(click, camera)
    const intersects2 = raycaster.intersectObjects(wv1)
    for(const intersect of intersects2) {
        parent.style.display = "block";
    }
})

window.addEventListener('click', (event) => {
    click.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	click.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    raycaster.setFromCamera(click, camera)
    const intersects2 = raycaster.intersectObjects(mr1)
    for(const intersect of intersects2) {
        parent2.style.display = "block";
    }
})

window.addEventListener('click', (event) => {
    click.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	click.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    raycaster.setFromCamera(click, camera)
    const intersects2 = raycaster.intersectObjects(fl1)
    for(const intersect of intersects2) {
        parent3.style.display = "block";
    }
})

/**
 * Animate
 */
document.addEventListener('mousemove', onDocumentMouseMove)

let MouseX = 0
let MouseY = 0

let TargetX = 0
let TargetY = 0

const WindowHalfX = window.innerWidth / 2
const WindowHalfY = window.innerHeight / 2

function onDocumentMouseMove(event) {
    MouseX = (event.clientX - WindowHalfX)
    MouseY = (event.clientY - WindowHalfY)
}

const clock = new THREE.Clock()

const tick = () =>
{
    TargetX = MouseX * 0.001
    TargetY = MouseY * 0.001 
 
    const elapsedTime = clock.getElapsedTime()
    sphere.rotation.y = .5 * elapsedTime
    sphere.rotation.y = .5 * (TargetX - sphere.rotation.y)
    sphere.rotation.x = .1 * (TargetY - sphere.rotation.x)
    sphere.rotation.z = -.1 * (TargetY - sphere.rotation.x)
    particlesM.rotation.y = .01 * elapsedTime
    particlesM.rotation.y = .1 * (TargetX - sphere.rotation.y)
    particlesM.rotation.x = .05 * (TargetY - sphere.rotation.x)
    particlesM.rotation.z = -.05 * (TargetY - sphere.rotation.x)


    wvpivot.rotation.y = .1 * elapsedTime
    wvpivot.rotation.x = .1 * elapsedTime
    wvpivot.rotation.z = .1 * elapsedTime
    WVr.lookAt(camera.position)
    FLr.lookAt(camera.position)
    MRr.lookAt(camera.position)

    // Raycaster

    raycaster.setFromCamera(pointer, camera)
    const intersects = raycaster.intersectObjects(clicable)
    
    for(const intersect of intersects) {
        gsap.to(intersect.object.scale, {x: 1.7, y: 1.7})
        //gsap.to(intersect.object.position, {z: 0.001})
        gsap.to(WVrt.scale, {x: 80000, y: 80000})
        gsap.to(WVrt.position, {x: 0.36, z: 0.9})
        gsap.to(FLrt.scale, {x: 80000, y: 80000})
        gsap.to(FLrt.position, {x: 0.26, z: 0.9})
        gsap.to(MRrt.scale, {x: 80000, y: 80000})
        gsap.to(MRrt.position, {x: 0.26, z: 0.9})
        
    }

    for(const clicables of clicable) {
        if (!intersects.find(intersect => intersect.object === clicables)) {
            gsap.to(clicables.scale, {x: 1, y: 1})
            //gsap.to(clicables.position, {z: 0})
            gsap.to(WVrt.scale, {x: 1, y: 1})
            gsap.to(WVrt.position, {x: 0, z: 0})
            gsap.to(FLrt.scale, {x: 1, y: 1})
            gsap.to(FLrt.position, {x: 0, z: 0})
            gsap.to(MRrt.scale, {x: 1, y: 1})
            gsap.to(MRrt.position, {x: 0, z: 0})
        }
    }



 
    // Render

    renderer.render( scene, camera );
    
 
    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}
 
tick()